import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import "../../css/menuList.css"
import AboutImage from "../../assets/about_icon.png";
import CloseImage from "../../assets/cancel.png";
import AboutModal from "./AboutModal.js";

class MenuList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      valueArray: [
        { name: "L1 AVG", value: 0.4112, unit: "mm" },
        { name: "L2 AVG", value: 0.4312, unit: "mm" },
        { name: "L1 MIN", value: 0.5342, unit: "mm" },
        { name: "L2 MIN", value: 0.4212, unit: "mm" },
        { name: "L1 MAX", value: 0.5123, unit: "mm" },
        { name: "L2 MAX", value: 0.5212, unit: "mm" },
        { name: "L1 ECC", value: 0.4512, unit: "mm" },
        { name: "L2 ECC", value: 0.4312, unit: "mm" },
      ],
    };
  }
  callBackRoute = () => {
      this.props.callBackRoute();
  };
  render() {
    return (
      <div className="wrapper">
          <div className="heading_menu_list">
              <p>MENU</p>
              <img className="closebutton" src={CloseImage} onClick={() => this.callBackRoute()} />
          </div>
          <div className="containers">
              <div className="rows">
                <div className="menu-card">
                  <div className="menu-content">
                    <div className="menu-details">
                      <div>
                        <img className="menu_images" src={AboutImage} />
                      </div>
                      <div className="menu_name_list">
                        <AboutModal name={'About'}  />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
          </div>
      </div>
    );
  }
}

export default withRouter(MenuList);
